.mainBox{
	margin: 10px;
	background-color: #F9F3E6;
	margin-top: 20px;
}


.kanban {
	width: 100%;
	color: #000;
	background-color: #F9F3E6;
	height: 100%;
	/*border: 1px solid #000;*/

	display: flex;
  	flex-direction: row;
  	flex-wrap: nowrap;
  	justify-content: left;
  	overflow: auto;
}



 .column {
	/*width: 200px;*/
	color: #000;
	
	border: 1px solid #000;
	margin: 10px 0;
	margin-right: 30px;
	text-align: center;
	box-shadow: 5px 5px 5px #aaa;
	
	
	flex: 0 0 250px;
	display: flex;
	flex-direction: column;

}

.columnAdd{
	flex: 0 0 250px;
	display: flex;
	justify-content: center;
	margin: 10px 0;
	color: #000;
	background-color: #FFF;
	height: 100%;
	flex-direction: column-reverse;
	margin-right: 20px;
	/*position: fixed;
	right: 5px;
	top: 35px;*/
}


 .itemNew {
	width: 100%;
	color: #000;
	background-color: #EA9772;
	border: 1px solid #000;
	
	text-align: center;
	box-shadow: 5px 5px 5px #aaa;
	
	flex: 0 0 150px;
	
	

}

.infoBox{
	width: 100%;
	background-color: transparent;
	bottom: 0px;
	text-align: right;
	right: 0px;
	padding-right: 20px;
	font-size: 0.8em;
}